<template>
  <div class="container">
    <div class="search-dv">
      <van-search placeholder="请输入项目别名或工作项目" v-model="searchText"/>
    </div>
    <div class="main">
      <div class="main-row" style="width: 100%">
        <van-row>
          <van-col span="12" v-for="(item,index) in dispalyWorkDaily" :key="index">
            <div class="col-bkg" @click="showWorkDailyById(item.daily_id)">
                <div class="project-top">
                  <span>{{item.project_name}}</span>
                </div>
                <div class="project-bottom">
                  <span>{{item.work_intro}}</span>
                </div>
            </div>
          </van-col>
        </van-row>
      </div>
      <router-view/>
      <div style="height: 44px"></div>
      <div class="footer">
        <van-tabbar v-model="active">
          <van-tabbar-item to="/WorkDailyManage" icon="wap-home">首页</van-tabbar-item>
          <van-tabbar-item icon="add-o" @click="addWorkDaily">新增</van-tabbar-item>
          <van-tabbar-item to="/MyInfo" icon="contact">我的</van-tabbar-item>
        </van-tabbar>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "WorkDailyManage",
    beforeCreate() {
      document.querySelector('body').setAttribute('style', 'background:#f3f3f3')
    },
    beforeDestroy() {
      document.querySelector('body').removeAttribute('style')
    },
    data() {
      return {
        active: 0,
        searchText: '',
        imgUrl: require('../assets/images/work/card-background.png'),
      }
    },
    computed: {
      dispalyWorkDaily() {
        return this.$store.state.displayWorkDaily;
      }
    },
    mounted() {
      this.searchWorkDailyHandle();
    },
    methods: {
      /* 查询工作日报--start*/
      searchWorkDailyHandle() {
        this.$api.get('/iims/dailies/data?').then( res => {
          if (res.status === 1) {
            console.log(res);
            var datas = res.data;
            var obj = [];
            for(var i = 0; i < datas.length; i++) {
              obj.push(datas[i]); // 将数据逐一push到obj中
            }
            this.$store.commit('storeAllWorkDaily', obj); // 将数据保存到store中的allWorkDaily中
            this.$store.commit('storeDisplayWorkDaily', obj);
            // console.log(this.$store.state.allWorkDaily); // 打印store中保存的数据
          }
        }).catch(err => {
          console.log(err);
        });
      },
      /* 查询工作日报--end*/

      /* 进入工作日报详情页,获取点击的工作日报id--start */
      showWorkDailyById(id) {
        this.$store.commit('changeJudgeAdd',false);
        this.$router.push({
          name: 'EditWorkDaily',
          query: {workDailyId: id}
        })
      },
      /* 进入工作日报详情页,获取点击的工作日报id--end */
      addWorkDaily(){
        this.$store.commit('changeJudgeAdd', true);
        this.$router.push({
          name: 'EditWorkDaily',
          query: {workDailyId: -1}
        });
      },
    },
    watch: {
      searchText(key) {
        var data = this.$store.getters.doneDisplayWorkDaily(key); // 查询匹配到的工作日报数据
        this.$store.commit('storeDisplayWorkDaily', data);
      }
    }
  }
</script>

<style lang="less" scoped>
  .container {
    height: 100%;
    background-color: #f3f3f3;

    .van-search {
      background-color: #f3f3f3 !important;
      padding: 20px;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 1;

      .van-search__content {
        background-color: #fff;
        border-radius: 30px;

        .van-cell {
          color: #fe8f01;

          /deep/ .van-field__left-icon .van-icon {
            font-size: 22px;
          }
        }
      }
    }

    .main {
      display: flex;
      flex-flow: column;
      padding: 10px 10px;
      margin-top: 54px;

      .van-row {
        height: 20%;
        .van-col {
          padding: 8px;
          .col-bkg{
            display: flex;
            flex-flow: column;
            height: 200px;
            background: url('../assets/images/work/card-background.png');
            background-size: cover;
          }
        }

        .project-top {
          padding: 0 20px;
          text-align: center;
          margin-top: 32%;

          span {
            color: #fe8f01;
            font-weight: bold;
            display: -webkit-box;
            overflow: hidden;
            white-space: normal !important;
            text-overflow: ellipsis;
            word-wrap: break-word;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical
          }
        }

        .project-bottom {
          margin-top: 16%;
          padding: 0 10px;
          text-align: center;

          span {
            font-size: 0.6rem;
            display: -webkit-box;
            overflow: hidden;
            white-space: normal !important;
            text-overflow: ellipsis;
            word-wrap: break-word;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical
          }
        }
      }
    }
  }

</style>
